@import url(https://duncanritchie.github.io/jsChallenges/main.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  width:100vw;
  min-height:100vh;
  display:grid;
  -webkit-justify-content:center;
          justify-content:center;
  -webkit-align-items:center;
          align-items:center;
  font-family: 'Alegreya', serif;
}

.scientificCalculator {
  font-family:"Helvetica",sans-serif;
  margin-top:-50px;
  width:1200px;
  height:800px;
  display:grid;
  -webkit-justify-content:center;
          justify-content:center;
  background-color:tan;
  border-radius:15px;
  box-shadow: 0px 5px 2px rgba(92, 67, 36, 0.1);
}

.calculator {
  font-family:"Helvetica",sans-serif;
  margin-top:-50px;
  width:590px;
  height:800px;
  display:grid;
  -webkit-justify-content:center;
          justify-content:center;
  background-color:tan;
  border-radius:15px;
  box-shadow: 0px 5px 2px rgba(92, 67, 36, 0.1);
}

.scientificDisplay {
  width:1110px;
  margin:25px 0 5px;
  padding:25px;
  height:100px;
  background-color:beige;
  text-align:right;
  font-size:50px;
  display:grid;
  -webkit-align-items:center;
          align-items:center;
  border-radius:10px;
  box-shadow: inset 0px 0px 20px rgba(92, 67, 36, 0.3);
}

.display {
  width:500px;
  margin:25px 0 5px;
  padding:25px;
  height:100px;
  background-color:beige;
  text-align:right;
  font-size:50px;
  display:grid;
  -webkit-align-items:center;
          align-items:center;
  border-radius:10px;
  box-shadow: inset 0px 0px 20px rgba(92, 67, 36, 0.3);
}

.bothButtonSets {
  width: 1100px;
  height: 600px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content:space-between;
          justify-content:space-between;
}

.scientificButtons {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap;
  -webkit-justify-content:space-between;
          justify-content:space-between;
  width:550px;
  height:600px;
  font-size:30px;
}

.buttons {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap;
  -webkit-justify-content:space-between;
          justify-content:space-between;
  width:550px;
  height:600px;
  font-size:30px;
}

.button {
  width: 130px;
  height:110px;
  background-color:lightcoral;
  display:grid;
  -webkit-align-items:center;
          align-items:center;
  border-radius:10px;
  box-shadow: 0px 3px 2px rgba(0,0,0,0.1);
}

.button:hover {
  background-color:lightpink
}

.button:active {
  background-color:pink;
  box-shadow: inset 0px 5px 2px rgba(0,0,0,0.1);
}

@media (max-width:590px) {
  .calculator {
    width:100vw;
    height:calc(100vw*80/59);
    border-radius:calc(100vw*15/590);
    -webkit-align-items:center;
            align-items:center;
  }

  .display {
    -webkit-align-self:center;
            align-self:center;
    width:calc(100vw*500/590);
    margin:calc(100vw*25/590) 0 calc(100vw*5/590);
    padding:calc(100vw*25/590);
    height:calc(100vw*100/590);
    font-size:calc(100vw*50/590);
    border-radius:calc(100vw*10/590);
    box-shadow: inset 0px 0px calc(100vw*20/590) rgba(92, 67, 36, 0.3);
  }

  .buttons {
    width:calc(100vw*550/590);
    height:calc(100vw*600/590);
    font-size:calc(100vw*30/590);
  }

  .button {
    width: calc(100vw*130/590);
    height:calc(100vw*110/590);
    border-radius:calc(100vw*10/590);
    box-shadow: 0px 3px 2px rgba(0,0,0,0.1);
  }


}
